// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from '../App'
import moment from 'moment'

import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'

import test003 from '@/components/test003'

// 主页
import applyForTest from '@/views/apply-for-test'
import successList from '@/views/apply-success-list'
import verifyApplyInfo from '@/views/verify-apply-info'
import mytestdetail from '@/views/my-test-detail'


Vue.use(Router)
Vue.filter('dateFormat', function (value, type) { //时间格式化过滤
  return moment(value).format(type);
});

const router = new Router({
  // mode:'history',
  routes: [{
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld
    },
    {
      path: '/list',
      name: 'test003',
      component: test003
    }, {
      path: '/applyForTest',
      name: 'applyForTest',
      component: applyForTest
    }, {
      path: '/successList',
      name: 'successList',
      component: successList
    },
    {
      path: '/verifyApplyInfo',
      name: 'verifyApplyInfo',
      component: verifyApplyInfo
    },{
      path: '/mytestdetail',
      name: 'mytestdetail',
      component: mytestdetail
    }
  ]
})


Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: {
    App
  },
  template: '<App/>'
})



